.qa-manager {
    display: flex;
    flex-direction: column;
    min-height: 1px;
    padding: 0 5px 5px 0;
}

.qa-manager__ul-container {
    overflow: auto;
}

.qa-manager li {
    padding: 0px;
}

.no-touch .qa-manager li:last-child {
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
}

.qa-manager__header {
    margin-bottom: 20px;
}

.qa-manager__buttons {
    display: flex;
    padding: 10px 0 0 0;
}
.qa-manager__button-remove-all {
    margin-right: auto;
}

.qa-manager__simple-bar-container {
    overflow: hidden;
}

.qa-manager__simple-bar-container > div {
    height: 100%;
}

.qa-manager__create-new-container {
    padding: 10px;
    border-style: solid;
    border-width: 1px;
    border-color: rgba(0, 0, 0, 0.125);
    border-bottom-right-radius: 0.375rem;
    border-bottom-left-radius: 0.375rem;
    background-color: #eee;
}

.qa-manager__warning {
    background-color: #f8d7da;
    color: #842029;;
    border: 1px solid #f5c2c7;
    border-top-right-radius: 0.375rem;
    border-top-left-radius: 0.375rem;
    padding: 10px;
}

.touch .qa-manager {
    flex-direction: column-reverse;
}

.touch .qa-manager__header-container {
    order: 1
}

.touch .qa-manager__buttons {
    flex-direction: row-reverse;
    padding: 0 0 10px 0;
}
.touch .qa-manager__button-remove-all {
    margin-right: 5px;
    margin-left: auto !important;
}
.touch .qa-manager__buttons > button:not(:first-child) {
    margin-left: 0px;
    margin-right: 5px;
}

.touch .qa-manager__create-new-container {
    border-radius: 0;
    border-top-right-radius: 0.375rem;
    border-top-left-radius: 0.375rem;
}

.touch .qa-manager li:first-child {
    border-top-left-radius: 0;
    border-top-right-radius: 0;
}

.touch .qa-manager__button-create-new > label {
    display: none;
}
.no-touch .qa-manager__button-create-new > label {
    display: block;
}

.no-touch .qa-manager__button-create-new > i {
    display: none;
}
.touch .qa-manager__button-create-new > i {
    display: block;
}

@media only screen and (max-width: 370px) {
    .qa-manager__button-create-new > label {
        display: none !important;
    }

    .qa-manager__button-create-new > i {
        display: block !important;
    }
}