.qa-item-view-mode__question-btns {
    display: flex;
}

.no-touch .qa-item-view-mode .qa_tool__buttons-group{
    visibility: hidden;
}

.qa-item-view-mode:hover .qa_tool__buttons-group{
    visibility: visible;
}

.qa-item-view-mode__question {
    cursor: pointer;
    word-break: break-word;
    flex-grow: 1;
}

.qa-item-view-mode__question i::before {
    font-size: small;
    transition-duration: 0.5s;
    transition-property: transform;
    vertical-align: baseline;
}
.qa-item-view-mode__question--open i::before {
    transform: rotate(90deg);
}

.qa-item-view-mode__question h5 {
    display: inline;
}

.qa-item-view-mode__answer {
    white-space: pre-wrap;
    word-break: break-word;
    display: block;
    margin-left: 15px;
}