.qa-tool-tooltip{
    display: inline-block;
    position: relative;
}

.qa-tool-tooltip__content {
    position: absolute;
    border-radius: 0.375rem;
    border: 1px solid rgba(0, 0, 0, 0.125);
    padding: 10px;
    background-color: #198754;
    color: #fff;
    font-weight: 500;
    line-height: 1.2;
    left: 0;
    transform: translateX(0);
    z-index: 100;
    word-wrap: break-word;
    max-width: 300px;
}

.qa-tool-tooltip__content::after {
    content: " ";
    left: 10px;
    border: solid transparent;
    height: 0;
    width: 0;
    position: absolute;
    pointer-events: none;
    border-width: 10px;
    bottom: 100%;
    border-bottom-color: #198754;
  }

.qa-tool-tooltip__content::before {
    content: " ";
    left: 8px;
    border: solid transparent;
    height: 0;
    width: 0;
    position: absolute;
    pointer-events: none;
    border-width: 12px;
    bottom: 100%;
    border-bottom-color: rgba(0, 0, 0, 0.125);
  }