.qa-tool-content {
    display: flex;
    overflow: hidden;
}

.qa-tool-content__inner-container {
    display: flex;
    width: 100%;
}

.qa-tool-content__info {
    border-style: solid;
    border-width: 0 1px 0 0;
    border-color: gray;
    margin: 0 10px 5px 0;
    padding: 0 10px 0 0;
    max-width: 300px;
}

.qa-tool-content .qa-manager{
    width: 100%;
}

@media only screen and (max-width: 1024px) {

    .qa-tool-content__inner-container {
        flex-direction: column;
    }

    .qa-tool-content__info {
        border-width: 0 0 1px 0;
        margin: 0 0 10px 0;
        padding: 0 0 10px 0;
        max-width: 100%;
    }
}

@media only screen and (max-width: 400px) {
    .qa-tool-content h3 {
        font-size: 1.3rem;
    }
}

@media only screen and (max-width: 370px) {
    .qa-tool-content h3 {
        font-size: 1.2rem;
    }
    .qa-tool-content h5 {
        font-size: 1rem;
    }
}

.qa_tool__buttons-group {
    white-space: nowrap;
}

.qa_tool__buttons-group > button:not(:first-child) {
    margin-left: 5px;
}

.loading-indicator {
    width: 24px;
    height: auto;
}

.qa-tool-confirm-alert {
    width: 500px;
    border: 1px solid rgba(0, 0, 0, 0.125);
    border-radius: 0.375rem;
    padding: 30px;
    background-color: #eee;
}

.qa-tool-confirm-alert h5 {
    text-align: center;
}

.qa-tool-confirm-alert__btns {
    display: flex;
    justify-content: center;
}

@media only screen and (max-width: 500px) {
    .qa-tool-confirm-alert {
        width: 300px;
    }
    .qa-tool-confirm-alert h5 {
        font-size: 1rem;
    }
}