.qa-item-edit-mode__buttons > button {
    width: 42px;
    height: 38px;
}

.qa-item-edit-mode__title {
    display: flex;
    word-break: break-word;;
}

.qa-item-edit-mode__buttons img {
    margin-left: -4px;
    margin-top: -3px;
}

.qa-item-edit-mode__question-label {
    display: block;
}

.qa-item-edit-mode__question--invalid {
    border-color: rgb(245, 194, 199);
    background-color: #f8d7da;
}

.qa-item-edit-mode__btns-checkbox {
    display: flex;
    flex-direction: column;
    margin-left: auto;
}

.qa-item-edit-mode__checkbox {
    position: absolute;
    z-index: -1;
    opacity: 0;
}

.qa-item-edit-mode__checkbox+label {
    display: inline-flex;
    align-items: center;
    user-select: none;
    font-size: small;
}

.qa-item-edit-mode__checkbox+label::before {
    content: '';
    display: inline-block;
    width: 1em;
    height: 1em;
    flex-shrink: 0;
    flex-grow: 0;
    border: 1px solid #adb5bd;
    border-radius: 0.25em;
    margin-right: 5px;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 70% 70%;
}

.qa-item-edit-mode__checkbox:checked+label::before {
    border-color: #198754;
    background-color: #198754;
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3e%3cpath fill='%23fff' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/%3e%3c/svg%3e");
  }

  .qa-item-edit-mode__checkbox:not(:disabled):not(:checked)+label:hover::before {
    border-color: #198754;
  }

  .qa-item-edit-mode__checkbox:not(:disabled):active+label::before {
    background-color: rgba(25, 135, 84, 0.25);
    border-color: rgba(25, 135, 84, 0.25);
  }

  .qa-item-edit-mode__checkbox:focus+label::before {
    box-shadow: 0 0 0 0.2rem rgba(25, 135, 84, 0.25);
  }

  .qa-item-edit-mode__checkbox:focus:not(:checked)+label::before {
    border-color: #198754;
  }

  .qa-item-edit-mode__checkbox:disabled+label::before {
    background-color: #fff;
  }